.container {
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    max-width: 900px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .container {
    max-width: 1000px;
    margin: 0 auto;
  }
}
.margin-both {
  margin: 0 10px 0 10px;
}

.page {
  font-size: 22px;
  font-family: "Roboto", "Arial", sans-serif;
}

.page__body {
  background-color: #000000;
  color: #fff;
}

.page__wrapper {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100vh;
  margin: 0 auto;
}

.page-header {
  display: grid;
  grid-template-columns: 1fr 4fr 1fr;
  grid-template-rows: repeat(2, min-content);
  justify-items: center;
  align-items: center;
  row-gap: 15px;
  margin-bottom: 10px;
}

.page-header__logo {
  justify-self: center;
  margin-top: 5px;
}

.page-header__nav {
  grid-column: 1/-1;
}

.page-header__title {
  font-family: "Kelly Slab", "Arial", sans-serif;
  letter-spacing: 2px;
  margin: 0;
  text-align: center;
  font-size: 22px;
}

.page-header__author {
  font-family: "Kelly Slab", "Arial", sans-serif;
  letter-spacing: 3px;
  margin: 0;
  text-align: center;
  font-size: 16px;
}

@media (min-width: 450px) {
  .page-header__title {
    font-size: 30px;
    letter-spacing: 3px;
  }
  .page-header__author {
    font-size: 20px;
  }
}
@media (min-width: 768px) {
  .page-header__title {
    font-size: 40px;
    letter-spacing: 5px;
  }
  .page-header__author {
    font-size: 25px;
  }
  .page-header__logo img {
    width: 45px;
  }
}
@media (min-width: 1200px) {
  .page-header__title {
    font-size: 50px;
  }
  .page-header__author {
    font-size: 30px;
  }
}
.burger-button__burger {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 20px;
  top: 20%;
}

.burger-button__burger span {
  height: 2px;
  width: 100%;
  transform: scale(1);
  background-color: #fff;
  border-radius: 10px;
}

.burger-button__burger::before {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: #fff;
  transition: all 0.3s ease 0s;
  top: 0px;
  border-radius: 10px;
}

.burger-button__burger::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  background-color: #fff;
  transition: all 0.3s ease 0s;
  bottom: 0;
  border-radius: 10px;
}

.burger-button__burger.active span {
  transform: scale(0);
}

.burger-button__burger.active::after {
  top: 50%;
  transform: rotate(45deg);
}

.burger-button__burger.active::before {
  top: 50%;
  transform: rotate(-45deg);
}

.link {
  color: #fff;
}

.main-nav {
  display: none;
}
.main-nav__list {
  list-style: none;
  font-size: 16px;
  text-align: center;
  margin: 0;
  padding: 0;
  line-height: 25px;
  text-transform: uppercase;
  letter-spacing: 3px;
}
.main-nav__link {
  color: #fff;
  text-decoration: none;
}

.picture-menu {
  display: flex;
  flex-direction: column;
}
.picture-menu__image {
  width: 100%;
  height: auto;
}
.picture-menu__link :hover {
  opacity: 85%;
  color: gray;
}

.picture-menu__list {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.picture-menu__item {
  position: relative;
}

.picture-menu__item--shadow picture::before {
  content: "";
  z-index: 0;
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  background: linear-gradient(to top, rgb(17, 17, 17), transparent);
}

.picture-menu__description {
  font-family: "Kelly Slab", "Arial", sans-serif;
  letter-spacing: 2px;
  color: #fff;
  font-size: 18px;
  position: absolute;
  bottom: -15px;
  left: 5px;
}

@media (min-width: 450px) {
  .picture-menu__description {
    font-size: 22px;
    bottom: -20px;
  }
}
@media (min-width: 768px) {
  .picture-menu__list {
    display: grid;
    grid-template-areas: "slider" "button button";
    column-gap: 10px;
  }
  .picture-menu__description {
    font-size: 28px;
    bottom: -25px;
    left: 10px;
  }
}
@media (min-width: 1200px) {
  .picture-menu__description {
    font-size: 30px;
    bottom: -25px;
    left: 10px;
  }
}
.swiper {
  max-width: 95vw;
  width: 100%;
  height: auto;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  color: #fff;
  font-size: 25px;
  opacity: 0.7;
}

.show {
  display: block;
  animation: burgerOpen 0.7s;
}

@keyframes burgerOpen {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
